import React from 'react'
import { HashRouter, Route, Switch, Redirect } from 'react-router-dom'

import About from './About'
import Background from './Background'
import BookListPage from './BookListPage'
import Breadcrumb from './Breadcrumb'
import ChapterListPage from './ChapterListPage'
import ChapterTextPage from './ChapterTextPage'
import Footer from './Footer'
import Home from './Home'
import InsightListPage from './InsightListPage'
import LoginScreen from './LoginScreen'
import Navbar from './Navbar'
import NotFound from './NotFound'
import Unauthorized from './Unauthorized'
import VolumeListPage from './VolumeListPage'
import useFirebaseUser from '../firebase/FirebaseUser'

function ComingSoon() {
  return (
    <header className="coming-soon">
      <h1 className="py-4 font-weight-bold">Enjoy the Scriptures</h1>
      <p className="py-4 font-weight-bold">Coming Soon</p>
    </header>
  )
}

function App() {
  const userState = useFirebaseUser()

  if (userState.status === 'LOGGED_OUT') {
    return (
      <HashRouter basename='/'>
        <div className="App">
          <Switch>
            <Route exact path="/login" component={LoginScreen} />
            <Route component={ComingSoon} />
          </Switch>
        </div>
      </HashRouter>
    )
  }

  return (
    <HashRouter basename='/'>
      <div className="App">
        <Navbar />
        <Route path='/' component={Breadcrumb} />

        <Route exact path="/" component={Background} />

        <div className="container py-5">
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/about" component={About} />

            {/* Scripture Notebook */}
            <Route exact path="/scriptures" component={VolumeListPage} />
            <Route exact path="/scriptures/:volume" component={BookListPage} />
            <Route exact path="/scriptures/:volume/:book" component={ChapterListPage} />
            <Route exact path="/scriptures/:volume/:book/:chapter" component={ChapterTextPage} />
            <Route exact path="/scriptures/:volume/:book/:chapter/:verse" component={InsightListPage} />

            {/* Topical Index */}
            <Route exact path="/topics" />

            {/* Login Related */}
            <Route exact path="/login" component={LoginScreen} />
            <Route exact path="/unauthorized" component={Unauthorized} />

            {/* 404 */}
            <Route exact path="/not_found" component={NotFound} />
            <Redirect to="/not_found" />
          </Switch>
        </div>

        {/* Invisble placeholder to prevent the sticky footer from covering content */}
        <div className="my-5"></div>

        <Footer />
      </div>
    </HashRouter>
  );
}

export default App
