import React from 'react'
import PropTypes from 'prop-types'

import Insight from '../model/Insight'
import SingleInsight from './SingleInsight'
import Verse from '../model/Verse'
import useFirebaseUser from '../firebase/FirebaseUser'

function buildInsight(user, verse, i, toggle, setInsight, idx) {
  function editInsight() {
    setInsight(i)
    toggle()
  }

  const btn = <button
    key={'edit-' + i.id}
    type="button"
    className="btn btn-warning"
    onClick={editInsight}>Edit Insight</button>

  return (
    <li key={idx}>
      <SingleInsight key={i.id} verse={verse} insight={i} />
      {user.status === 'LOGGED_IN' ? btn : null}
    </li>
  )
}

function InsightGroup({ verse, insightGroup, toggle, setInsight }) {
  const user = useFirebaseUser()

  let f = insightGroup[0].fragment
  let phrase = verse.text.split(' ').slice(f[0], f[1]).join(' ')

  return (
    <>
      <div key={f}>
        <hr />
        <p className="text-primary font-italic">{phrase}</p>
      </div>
      <ol>
        {insightGroup.map((i, idx) => buildInsight(user, verse, i, toggle, setInsight, idx))}
      </ol>
    </>
  )
}

InsightGroup.propTypes = {
  verse: PropTypes.instanceOf(Verse).isRequired,
  insightGroup: PropTypes.arrayOf(PropTypes.instanceOf(Insight)),
}

export default InsightGroup
